const sourceMap = {
  tpi: new URL("TPI Ribbon Cutting.webm", import.meta.url),
  mmb: new URL("MMB 2023 Highlights.webm", import.meta.url),
  calibr: new URL("Calibr V5.webm", import.meta.url),
  lvf: new URL("Luther Vandross Foundation.webm", import.meta.url),
  uncf: new URL("UNCF Faith-Based Message.webm", import.meta.url),
};

function isKeyofSourceMap(key?: string): key is keyof typeof sourceMap {
  return !!key && key in sourceMap;
}

const buttons = document.querySelectorAll("button");

buttons.forEach((button) => {
  button.addEventListener("click", (event) => {
    const key = (event.currentTarget as HTMLButtonElement).dataset.videoKey;
    if (isKeyofSourceMap(key)) replaceVideo(sourceMap[key].href);
  });
});

function replaceVideo(nextSource: string) {
  const video = document.querySelector("#video") as HTMLSourceElement;
  const nextVideo = document.createElement("video");
  const nextVideoSrc = document.createElement("source");
  nextVideoSrc.src = nextSource;
  nextVideoSrc.type = "video/webm";
  nextVideo.appendChild(nextVideoSrc);
  nextVideo.id = "video";
  nextVideo.controls = true;
  nextVideo.autoplay = true;

  video.replaceWith(nextVideo);
}
